<!-- 玫瑰图 -->

<template>
  <div class="demo">
    <div class="box">
      <h3>玫瑰图-内部图形标签</h3>
      <div id="container"></div>
    </div>
    <div class="box">
      <h3>玫瑰图-设置条件状态</h3>
      <div id="containerG2"></div>
    </div>
    <div class="box">
      <h3>玫瑰图-元素交互</h3>
      <div id="containerGz"></div>
    </div>
  </div>
</template>

<script>
import { Rose } from '@antv/g2plot'

export default {
  components: {},
  data() {
    return {}
  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const data = [
      { type: '分类一', value: 27 },
      { type: '分类二', value: 25 },
      { type: '分类三', value: 18 },
      { type: '分类四', value: 15 },
      { type: '分类五', value: 10 },
      { type: '其他', value: 5 }
    ]

    const rosePlot = new Rose('container', {
      data,
      xField: 'type',
      yField: 'value',
      seriesField: 'type',
      radius: 0.9,
      label: {
        offset: -15
      }
    })

    rosePlot.render()

    this.pointFun()
    this.fun()
  },
  methods: {
    pointFun() {
      const data = [
        { type: '分类一', value: 27 },
        { type: '分类二', value: 25 },
        { type: '分类三', value: 18 },
        { type: '分类四', value: 15 },
        { type: '分类五', value: 10 },
        { type: '其他', value: 5 }
      ]

      const rosePlot = new Rose('containerG2', {
        data,
        xField: 'type',
        yField: 'value',
        seriesField: 'type',
        radius: 0.9,
        // 设置 active 状态样式
        state: {
          active: {
            style: {
              lineWidth: 0,
              fillOpacity: 0.65
            }
          }
        },
        legend: {
          position: 'bottom'
        },
        interactions: [{ type: 'element-active' }]
      })

      rosePlot.render()
    },
    fun() {
      const data = [
        { type: '分类一', value: 27 },
        { type: '分类二', value: 25 },
        { type: '分类三', value: 18 },
        { type: '分类四', value: 15 },
        { type: '分类五', value: 10 },
        { type: '其他', value: 5 }
      ]

      const rosePlot = new Rose('containerGz', {
        data,
        xField: 'type',
        yField: 'value',
        seriesField: 'type',
        radius: 0.9,
        legend: {
          position: 'bottom'
        },
        interactions: [{ type: 'element-active' }]
      })

      rosePlot.render()
    }
  },
  created() {
    // var wrapperElement = '<div style=position:relative;></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo {
  display: flex;
  flex-flow: wrap;

  .box {
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display: inline-block;
    padding: 0 20px 20px;
    margin: 20px;

    h3 {
      line-height: 50px;
      height: 50px;
      text-align: center;
    }
  }

  #container {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerG2 {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGz {
    display: block;
    width: 500px;
    height: 300px;
  }
}
</style>



